import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { College } from '../entity';
import { SchoolService } from './school.service';

@Component({
  selector: 'app-college-detaill',
  templateUrl: './college-detaill.component.html',
  styleUrls: ['./college-detaill.component.css']
})
export class CollegeDetaillComponent implements OnInit {

  @Input() college: College;

  constructor(
    private route: ActivatedRoute,
    private schoolService: SchoolService,
    private location: Location
  ) {}

  ngOnInit(): void {
    this.route.queryParams
           .subscribe((params: College) => {
               this.college = params;
             });
  }

  goBack(): void {
    this.location.back();
  }
}
