import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { SchoolComponent } from './school/school.component';
import { TeacherComponent } from './teacher/teacher.component';
import { CollegeDetaillComponent } from './school/college-detaill.component';
import { AddCollegeComponent } from './school/add-college.component';
import { UpdateCollegeComponent } from './school/update-college.component';


const routes: Routes = [
  { path: '', redirectTo: '/school.ctl', pathMatch: 'full' },
  { path: 'school.ctl', component: SchoolComponent},
  { path: 'teacher', component: TeacherComponent},
  { path: 'addCollege', component: AddCollegeComponent},
  { path: 'updateCollege/:id', component: UpdateCollegeComponent},
  { path: 'detail/:id', component: CollegeDetaillComponent}
  ];
@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}

