import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { College } from '../entity';
import {Params} from '@angular/router';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class SchoolService {

  private collegeUrl = '../school.ctl';
  constructor(private http: HttpClient) { }

  /** GET colleges from the server */
  getColleges(): Observable<College[]> {
    return this.http.get<any>(this.collegeUrl).pipe(
         catchError(this.handleError<College[]>('getColleges', []))
      );
  }
  /** GET college by id. Will 404 if id not found */
  getCollege(id: Observable<Params>): Observable<College> {
    const url = this.collegeUrl + '?id=' + id;
    return this.http.get<College>(url).pipe(
      catchError(this.handleError<College>(`getCollege id=${id}`))
    );
  }

  /** POST: add a new college to the server */
  addCollege(college: { no: string; description: string; remarks: string }): Observable<College> {
    return this.http.post<College>(this.collegeUrl, college, httpOptions).pipe(
      catchError(this.handleError<College>('addCollege'))
    );
  }

  /** DELETE: delete the college from the server */
  deleteCollege(college: College | number): Observable<College> {
    const id = typeof college === 'number' ? college : college.id;
    return this.http.delete<any>(this.collegeUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<College>())
    );
  }

  /** PUT: update the hero on the server */
  updateCollege(college: College): Observable<any> {
    return this.http.put(this.collegeUrl, college, httpOptions).pipe(
      catchError(this.handleError<any>())
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }


}

